<template>
  <div>
    <!-- 添加座位弹出层 -->
    <div class="tablehome">
      <el-dialog title="确认票务信息" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="剧目名称" :label-width="formLabelWidth">
            <el-input v-model="form.movieName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="上映影厅" :label-width="formLabelWidth">
            <el-input v-model="form.movieHall" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="座位编号" :label-width="formLabelWidth">
            <el-input v-model="form.tableNumber_rel" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="价格(万)" :label-width="formLabelWidth">
            <el-input v-model="form.movieMoney" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">返回修改</el-button>
          <el-button type="primary" @click="(dialogFormVisible = false,addTicket())">确认抢票</el-button>
        </div>
      </el-dialog>
      <!--  hearder 导航栏部分 -->
      <!-- 影院首页 轮播图部分 -->
      <div class="use-lbt">
        <div class="use_header">
          <div class="use_left">
            <span>
              <em>TTMS</em>
            </span> Attentively write bug theater
          </div>
          <div class="use_center"></div>
          <div class="use_right"></div>
        </div>
        <div class="ticket_show" v-show="show_01">
          <div class="ticket_img">
            <img :src="obj.movieImg" alt />
          </div>
          <div class="ticket_duce">
            <p>影片介绍：{{obj.movieDuce}}</p>
            <p>影片时长： {{obj.movieDuration}}</p>
            <p>影片来源：{{ obj.movieFrom}}</p>
          </div>
          <div class="btn">
            <el-button @click="showChange()">点击选座</el-button>
            <el-button @click="jump()">退出抢票</el-button>
          </div>
        </div>
        <div class="ticket_show2" v-show="show_02">
          <div class="movieHall">{{obj.movieHall}}座位分布图</div>
          <div class="table-show">
            <div
              class="table-a"
              v-for="item in movieHallTableList"
              :key="item.id"
              @click="handleClick(item)"
            >
              <img src="../../assets/0.png" alt v-show="item.tableState == 'ready'" />
              <img src="../../assets/1.png" alt v-show="item.tableState == 'select'" />
              <img src="../../assets/2.png" alt v-show="item.tableState == 'selected'" />
            </div>
            <div class="tabledetail">
              <p>座位信息：{{tableNumber01}}</p>
              <p>座位状态：{{tableState01}}</p>
              <p>座位所属影厅：{{tableHall01}}</p>
              <div class="table-btn">
                <el-button @click="dialogFormVisible = true" :disabled="selected">进入抢票</el-button>
                <el-button @click="showChange()">退出选座</el-button>
              </div>
            </div>
          </div>
        </div>

        <vue-particles
          color="#fff"
          :particleOpacity="0.7"
          :particlesNumber="60"
          shapeType="circle"
          :particleSize="4"
          linesColor="#fff"
          :linesWidth="1"
          :lineLinked="true"
          :lineOpacity="0.4"
          :linesDistance="150"
          :moveSpeed="2"
          :hoverEffect="true"
          hoverMode="grab"
          :clickEffect="true"
          clickMode="push"
          class="lizi"
        ></vue-particles>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      obj: {},
      movieHallTableList: [],
      tableNumber01: "",
      tableState01: "",
      tableHall01: "",
      show_01: true,
      show_02: false,
      ticketList: null,
      imgSrc: "../../assets/0.png",
      dialogTableVisible: false,
      dialogFormVisible: false,
      movieMoneyAll: "",
      selected: "",
      form: {
        movieName: "",
        movieHall: "",
        movieMoney: 10,
        tableNumber_rel: "",
        movieClass: "",
        movieDate: "",
        delivery: false
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    jump() {
      this.$router.push({ path: "/movieplan" });
    },
    handleClick(e) {
      console.log("座位信息");
      console.log(e);
      this.form.tableNumber_rel = e.tableNumber;
      this.tableNumber01 = e.tableNumber;
      this.tableHall01 = e.tableHallName;
      if (e.tableState == "ready") {
        this.tableState01 = "等待入座";
        this.selected = false;
        //  修改座位状态
        axios({
          url: "http://localhost:2020/ticket_select",
          method: "POST",
          data: {
            tableNumber_rel: e.tableNumber,
            tableHallName: e.tableHallName
          }
        });
      } else if (e.tableState == "select") {
        this.tableState01 = "座位正在选择中,可抢";
        this.selected = false;
      } else {
        this.tableState01 = "已被选择";
        this.selected = "disabled";
      }

      // alert(this.tableState01)
      //  if(this.tableState01 == '已被选择'){

      //  }
    },
    showChange() {
      (this.show_01 = !this.show_01), (this.show_02 = !this.show_02);
    },
    addTicket() {
      //抢票数据提交

      axios({
        url: "http://localhost:2020/ticket_buy",
        method: "POST",
        data: {
          //用户名称
          username: JSON.parse(this.$store.state.currentUser).username,
          //影厅名称
          movieHall: this.form.movieHall,
          //剧目名称
          movieName: this.form.movieName,
          //票的价格
          movieMoney: this.form.movieMoney,
          //座位编号
          tableNumber_rel: this.form.tableNumber_rel,
          //上映日期
          movieDate: this.form.movieDate,
          //剧目类型
          movieClass: this.form.movieClass,
          ticketList: this.ticketList,
          movieMoneyAll: this.movieMoneyAll
        }
      }).then(res => {
        console.log(res);
        alert("恭喜你，购票成功，可前往个人中心查看票务详情！");
        this.$router.push({ path: "/movieplan" });
      });
    }
  },
  created() {
    var obj = this.$route.query;

    this.obj = obj;

    console.log("剧目信息");
    console.log(obj);
    this.form.movieName = obj.movieName;
    this.form.movieHall = obj.movieHall;
    this.form.movieMoney = 10;
    this.form.movieClass = obj.movieClass;
    this.form.movieDate = obj.movieDate;
    this.ticketList = obj.ticketList;
    this.movieMoneyAll = obj.movieMoney;

    // 请求座位数据
    axios({
      url: "http://localhost:2020/movieDetail",
      method: "POST",
      data: {
        movieHallName: obj.movieHall
      }
    }).then(res => {
      // console.log(res);

      var arr2 = [];
      for (let i in res.data) {
        arr2.push(Object.assign(res.data[i], { imgSrc: "../../assets/0.png" }));
      }

      console.log(arr2);
      this.movieHallTableList = arr2;
    });
  }
};
</script>

<style>
.use_header {
  width: 100%;
  height: 60px;
  display: flex;
}

.use_left {
  flex: 3;
  line-height: 60px;
  color: white;
  text-align: left;
  padding-left: 30px;
  font-size: 18px;
}

.use_left span {
  font-size: 40px;
  color: rgb(241, 135, 13);
  font-weight: 600;
}

.use_center {
  flex: 2;
}

.use_right {
  flex: 5;
  display: flex;
  line-height: 60px;
}

.use_right .item {
  flex: 1;
  color: rgb(161, 157, 157);
  cursor: pointer;
}

.use_right .item.active {
  box-sizing: border-box;
  color: white;
}

.table-btn {
  width: 250px;
  height: 200px;
  float: right;
  /* border: 2px solid red; */
}

