  height: 45%;
  /* border-left: 2px solid rgb(209, 206, 206);
    border-right: 2px solid rgb(209, 206, 206);
    border-bottom: 2px solid rgb(209, 206, 206); */
  box-sizing: border-box;
  background: white;
  z-index: 9;
  text-align: left;
  padding: 10px;
  text-indent: 2em;
  font-size: 14px;
  border-radius: 0 0 20px 20px;
  box-shadow: 2px 2px 2px rgb(128, 125, 125);
}

.ticket_comment {
  width: 100%;
