<template>
  <div class="admin-box">
    <div class="header">
        <div class="title"><span><em>TTMS </em> </span> -影院后台管理系统</div>
        <div class="user">{{id}}:{{name}} 
        <a @click="exit" id="exit">退出系统</a></div>
    </div>
    <div class="left">
      <el-row class="tac" style="height:100%;">
        <el-col :span="20">
          <el-menu
            default-active="1"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            background-color="#EFF1F6"
            text-color="black"
            active-text-color="#409EFF"
          >
            <router-link to="/manager/movie-manager"><el-menu-item index="1">
              <i class="el-icon-menu"></i>
              <span slot="title">管理剧目 </span>
            </el-menu-item></router-link>
            <router-link to="/manager/promance-plan"><el-menu-item index="2">
              <i class="el-icon-menu"></i>
              <span slot="title">安排演出</span>
            </el-menu-item></router-link>
            <router-link to="/manager/saleData"><el-menu-item index="3">
              <i class="el-icon-menu"></i>
              <span slot="title">销售数据</span>
            </el-menu-item></router-link>
            
          </el-menu>
        </el-col>
      </el-row>
    </div>
    <div class="right">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      name:'',
      id:''
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    exit(){
      this.$router.push({path:'/login'})
    },
     isLogin(){
        this.$store.commit('userStatus',sessionStorage.getItem('userInfo'));
    },
  },
  created(){
    this.isLogin();
    console.log(JSON.parse(this.$store.state.currentUser))
    this.id = JSON.parse(this.$store.state.currentUser).identify;
    this.name = JSON.parse(this.$store.state.currentUser).username;
  }
};
</script>

<style>
.admin-box {
  width: 100%;
  height: 100%;
  border: 0.1px solid transparent;
  position: relative;
}

.header {
  width: 100%;
  height: 50px;
  /* position: relative; */
  line-height: 50px;
  background:#409EFF;
  text-indent: 1em;
  /* border: 2px solid red; */
  display: flex;
  font-family: "华文新魏";
}

.header .title{
  flex: 7;
  /* border: 2px solid green; */
  text-align: center;
  font-size: 27px;
  color: white;
}

.header .user{
  flex: 3;
  /* border:2px solid  violet; */
  font-size: 20px;
  color: white;
}

.header span{
  color: rgb(236, 117, 62);
  font-size: 35px;
}

.left {
  box-sizing: border-box;
  vertical-align: top; /*两个inline-block的盒子，必须两个子元素都顶端对齐。*/
  width: 250px;
  height: calc(100% - 50.2px);
  float: left;
  /* border: 2px solid red; */
  position: relative;
  background:#EFF1F6;
}


.right {
  box-sizing: border-box;
  float: left;
  vertical-align: top;
  width: -webkit-calc(
    100% - 250px
  ); /*减号前后要有空格，否则无效，减去的宽度必须计算清楚*/
  height: calc(100% - 50.2px);
  /* border: 2px solid green; */
}

.el-menu-vertical-demo{
  position: absolute !important;
  width: 100% !important;
}

.el-menu{
  border: none !important;
}


</style>