<template>
  <div class="login-container">
    <el-form
      status-icon
      ref="ruleForm2"
      label-position="left"
      label-width="0px"
      class="demo-ruleForm login-page"
    >
      <h3 class="title">
        <label for>用心写bug影院</label>
      </h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="userName" auto-complete="off" placeholder="用户名"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="passWord" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" id="btn1" style="width:100%;" @click="handleSubmit">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      userName: "",
      passWord: ""
    };
  },
  methods: {
    // userInfo(payload){
    //   console.log("Login 登陆发送数据")
    //    this.$store.commit('getUserInfo',payload)
    // },
    handleSubmit() {
      axios({
        url: "http://localhost:2020/login",
        method: "post",
        data: {
          username: this.userName,
          password: this.passWord
        }
      }).then(res => {
        console.log(res);
         var obj = {
                 username: res.data.username,
                 identify: res.data.identify
               }
        //根据不同身份跳转到不同的管理页面
        if (res.data.identify == "对不起，您无权操作后台管理系统！") {
          alert(res.data.identify);
        }

        if (res.data.identify == "系统管理员") {
          alert(`登陆成功！欢迎${res.data.identify}登陆后台管理系统！`);
          // this.userInfo(res.data);

          //将用户信息放入sessionStorage中
          sessionStorage.setItem('userInfo',JSON.stringify(res.data));

          //将用户信息放入vuex
          this.$store.dispatch('setUser',JSON.stringify(res.data));

          this.$router.push({
            path:"/admin",
            // query: {
            //   username: res.data.username,
            //   identify: res.data.identify
            // }
          });
        }

        if (res.data.identify == "剧院经理") {
          alert(`登陆成功！欢迎${res.data.identify}登陆后台管理系统！`);
          // this.userInfo(res.data);

          //将用户信息放入sessionStorage中
          sessionStorage.setItem('userInfo',JSON.stringify(res.data));

          //将用户信息放入vuex
          this.$store.dispatch('setUser',JSON.stringify(res.data));
          this.$router.push({
            path: "/manager",
            // query: {
            //   username: res.data.username,
            //   identify: res.data.identify
            // }
          });
        }

        if (res.data.identify == "售票员") {
          alert(`登陆成功！欢迎${res.data.identify}登陆后台管理系统！`);
          // this.userInfo(res.data);

          //将用户信息放入sessionStorage中
          sessionStorage.setItem('userInfo',JSON.stringify(res.data));

          //将用户信息放入vuex
          this.$store.dispatch('setUser',JSON.stringify(res.data));
          this.$router.push({
            path: "/ticket",
            // query: {
            //   username: res.data.username,
            //   identify: res.data.identify
            // }
          });
        }

        if (res.data.identify == "普通用户") {
          alert(`登陆成功！欢迎${res.data.identify}登陆后台管理系统！`);
          // this.userInfo(res.data);

          //将用户信息放入sessionStorage中
          sessionStorage.setItem('userInfo',JSON.stringify(res.data));

          //将用户信息放入vuex
          this.$store.dispatch('setUser',JSON.stringify(res.data));
          this.$router.push({
            path: "/users",
            // query: {
            //   username: res.data.username,
            //   identify: res.data.identify
            // }
          });
        }
      });
    }
  }
};
</script>

<style>
.login-container {
  width: 100%;
  height: 100%;
  border: 2px solid transparent;
  /* background: black; */
  background-image: url("../../assets/back.jpg");
  background-size: 100% 100%;
}
.login-page {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  margin: 180px auto;
  width: 350px;
  padding: 25px 35px 25px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
label.el-checkbox.rememberme {
  margin: 0px 0px 15px;
  text-align: left;
}

.title label {
  color: #409eff;
  font-size: 30px;
  font-family: "华文新魏";
}
</style>

