<template>
  <!-- 添加剧目弹出层 -->
  <div class="tablehome">
    <el-dialog title="剧目上映计划" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="剧目名称">
          <el-input v-model="form.movieName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="上映日期">
          <el-input v-model="form.movieDate" autocomplete="off"></el-input>
        </el-form-item>
        <!-- <el-form-item label="电影状态">
          <el-input v-model="form.movieState" autocomplete="off"></el-input>
        </el-form-item>-->
        <el-form-item label="电影状态">
          <el-select v-model="form.movieState" placeholder="请选择电影状态" class="select">
            <el-option label="正在热映" value="正在热映"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="上映影厅">
          <el-select v-model="form.movieHall" placeholder="请选择剧目类型" class="select">
            <el-option
              v-for="item in movieHallList"
              :key="item.id"
              :label="item.movieHallName"
              :value="item.movieHallName"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="(dialogFormVisible = false,addMovie())">确认上映</el-button>
      </div>
    </el-dialog>
    <div class="home1">
      <div class="top1">影院演出安排</div>
      <div class="bottom1">
        <el-table
          :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
          stripe
          style="width: 100%"
        >
          <el-table-column prop="movieName" label="电影名称" width="180"></el-table-column>
          <el-table-column prop="movieHall" label="上映影厅" width="180"></el-table-column>
          <el-table-column prop="movieDate" label="上映日期"></el-table-column>
          <el-table-column prop="movieClass" label="电影类型"></el-table-column>
          <el-table-column prop="movieState" label="电影状态"></el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="success"
                :disabled="scope.row.movieState != '等待上映'"
                @click="(handleClick2(scope.row),dialogFormVisible = true)"
              >上映</el-button>
              <el-button
                size="mini"
                type="danger"
                
                @click="(handleClick(scope.row),dialogFormVisible2 = true)"
              >下架</el-button>
            </template>
          </el-table-column>
        </el-table>
        <template>
          <div class="page">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[2,3 , 4, 5]"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="tableData.length"
            ></el-pagination>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { filmsList } from "../../network/films";
import { movieHome } from "../../network/movieHome";
export default {
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      movieHallList: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogTableVisible2: false,
      dialogFormVisible2: false,
      form: {
        searchName: "",
        movieName: "",
        movieDate: "",
        movieDuration: "",
        movieHall: "",
        movieFrom: "",
        movieClass: "",
        movieState: "正在热映",
        movieMoney: "",
        state: "1"
      }
    };
  },
  created() {
    filmsList().then(res => {
      // console.log(res);

      this.tableData = res;
    });
    movieHome().then(res => {
      // console.log(res);

      var arr1 = [];
      for (let i in res) {
        if (res[i].movieHallStates == "等待营业") {
          arr1.push(res[i]);
        }
      }
      this.movieHallList = arr1;
    });
  },
  methods: {
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    handleClick2(e) {
      console.log(e._id);
      this._id = e._id;
      this.form.movieName = e.movieName;
      this.form.movieDate = e.movieDate;
      this.form.movieState = e.movieState;
      this.form.movieHall = e.movieHall;
      this.form.movieDuration = e.movieDuration;
      this.form.movieFrom = e.movieFrom;
      this.form.movieClass = e.movieClass;
      this.form.movieMoney = e.movieMoney;
      this.form.state = e.state;
    },
    handleClick(e) {
      // console.log(e);
      // this._id = e._id;
      // this.form.movieHall = e.movieHall;
      axios({
        url: "http://localhost:2020/outplayMovie",
        method: "post",
        data: {
          movieName: e.movieName,
          movieHall: e.movieHall
        }
      }).then(res => {
        alert("剧目下架成功！");
        location.reload();
      });
    },
    addMovie() {
      axios({
        url: "http://localhost:2020/playMovie",
        method: "post",
        data: {
          id: this._id,
          movieName: this.form.movieName,
          movieDate: this.form.movieDate,
          movieDuration: this.form.movieDuration,
          movieHall: this.form.movieHall,
          movieFrom: this.form.movieFrom,
          movieClass: this.form.movieClass,
          movieState: this.form.movieState,
          movieMoney: this.form.movieMoney,
          state: this.form.state
        }
      }).then(res => {
        // console.log(res);
        alert("剧目上映成功！");
        location.reload();
      });
    }
  }
};
</script>

<style>
.home1 {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
  position: relative;
}

.top1 {
  width: 100%;
  height: 70px;
  line-height: 70px;
  /* border: 2px solid red; */
  color: #409eff;
  font-size: 26px;
  font-family: "华文新魏";
  border-bottom: 2px solid rgb(224, 221, 221);
}

.bottom1 {
  width: 100%;
  height: calc(100% - 70px);
  /* border: 2px solid green; */
}
</style>