<template>
    <!-- 查看剧目弹出层 -->
    <div class="tablehome">
      <el-dialog title="查看剧目详情" :visible.sync="dialogFormVisible2">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="剧目名称">
            <el-input v-model="form.movieName" autocomplete="off" disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="剧目时长">
            <el-input v-model="form.movieDuration" autocomplete="off" disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="剧目来源">
            <el-input v-model="form.movieFrom" autocomplete="off" disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="电影类型">
            <el-select v-model="form.movieClass" placeholder="请选择剧目类型" disabled="true" class="select">
              <el-option label="搞笑/励志" value="搞笑/励志"></el-option>
              <el-option label="动画电影" value="动画电影"></el-option>
              <el-option label="冒险/奇幻" value="冒险/奇幻"></el-option>
              <el-option label="青春/励志" value="青春/励志"></el-option>
              <el-option label="爱情/喜剧" value="爱情/喜剧"></el-option>
            </el-select>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible2 = false">取 消</el-button>
          <el-button type="primary" @click="(dialogFormVisible2 = false)">确 定</el-button>
        </div>
      </el-dialog>

      <div class="home1">
        <div class="top11">
          <div class="left1">
            <el-form ref="form" :model="form" label-width="0px">
              <el-form-item label>
                <el-input v-model="form.searchName" placeholder="请输入你要搜索的电影名称......"></el-input>
              </el-form-item>
            </el-form>
            <el-row></el-row>
          </div>
          <div class="right1">
            <el-button type="primary" style="float:left;" @click="globalSearch">点击搜索</el-button>
            <el-button style="float:left;" @click="clear">重置剧目</el-button>
            <!-- <el-button type="success" style="float:right" @click="dialogFormVisible = true">添加剧目</el-button> -->
          </div>
        </div>
        <div class="bottom1">
          <el-table
            :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
            stripe
            style="width: 100%"
          >
            <el-table-column prop="movieName" label="电影名称" width="180"></el-table-column>
            <el-table-column prop="movieHall" label="上映影厅" width="180"></el-table-column>
            <el-table-column prop="movieDate" label="上映日期"></el-table-column>
            <el-table-column prop="movieClass" label="电影类型"></el-table-column>
            <el-table-column prop="movieState" label="电影状态"></el-table-column>
            <el-table-column prop="address" label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="(handleClick(scope.row),dialogFormVisible2 = true)"
                >查看</el-button>
                <!-- <el-button size="mini" type="danger" @click="handleClick2(scope.row)">删除</el-button> -->
              </template>
            </el-table-column>
          </el-table>
          <template>
            <div class="page">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[2,3 , 4, 5]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableData.length"
              ></el-pagination>
            </div>
          </template>
        </div>
      </div>
    </div>
</template>

<script>
import axios from "axios";
import { filmsList } from "../../network/films";
export default {
  data() {
    return {
      _id: "",
      arr1: "",
      arr2: "",
      arr3: "",
      tableData: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogTableVisible2: false,
      dialogFormVisible2: false,
      form: {
        movieName: "",
        movieDate: "------",
        movieDuration: "",
        movieHall: "------",
        movieFrom: "",
        movieClass: "",
        movieState: "等待上映",
        movieMoney: "",
        state: "1",
        searchName: ""
      }
    };
  },
  created() {
    filmsList().then(res => {
      // console.log(res);
      this.arr1 = res;
      this.tableData = this.arr1;
    });
  },
  methods: {
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    //增加剧目
    addMovie() {
      // console.log(this.form)
      axios({
        url: "http://localhost:2020/addMovie",
        method: "post",
        data: {
          movieName: this.form.movieName,
          movieDate: this.form.movieDate,
          movieDuration: this.form.movieDuration,
          movieHall: this.form.movieHall,
          movieFrom: this.form.movieFrom,
          movieClass: this.form.movieClass,
          movieState: this.form.movieState,
          movieMoney: this.form.movieMoney,
          state: this.form.state
        }
      }).then(res => {
        // console.log(res);
        alert(res.data);
        location.reload();
      });
    },
    // 修改用户
    handleClick(e) {
      console.log(e._id);
      this._id = e._id;
      this.form.movieName = e.movieName;
      this.form.movieDuration = e.movieDuration;
      this.form.movieFrom = e.movieFrom;
      this.form.movieClass = e.movieClass;
    
    },
    globalSearch() {
      axios({
        url: "http://localhost:2020/searchMovie",
        method: "post",
        data: {
          searchName: this.form.searchName
        }
      }).then(res => {
        this.arr2 = res.data;
        // console.log(res);
        this.tableData = this.arr2;
      });
    },
    clear() {
      this.tableData = this.arr1;
      this.form.searchName = "";
    }
  }
};
</script>

<style>
.home1 {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  position: relative;
}

.top11 {
  width: 100%;
  height: 70px;
  line-height: 70px;
  font-size: 40px !important;
  border-bottom: 2px solid rgb(224, 221, 221);
  display: flex;
}

#input222 {
  width: 80% !important;
}

.top11 .right1 .el-button {
  padding: 8px 8px !important;
}

.top11 .left1 {
  flex: 4;

  font-size: 40px;
  margin-top: 20px;
  height: 100%;
  text-align: center;
  margin-left: 50px;
  /* border: 2px solid red; */
}

.top11 .right1 {
  flex: 6;
  margin-top: 23px;
  height: 100%;
  padding: 0 30px 0 30px;
  /* border: 2px solid red; */
}

.bottom1 {
  width: 100%;
  height: calc(100% - 70px);
}
</style>