<template>
  <div class="home">
    <!-- 添加用户弹出层 -->
    <div class="tablehome">
      <el-dialog title="添加用户" :visible.sync="dialogFormVisible">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="用户名称">
            <el-input v-model="form.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="用户密码">
            <el-input v-model="form.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="用户权限">
            <el-select v-model="form.id" placeholder="请选择用户权限" class="select">
              <el-option label="管理员" value="管理员"></el-option>
              <el-option label="售票员" value="售票员"></el-option>
              <el-option label="剧院经理" value="剧院经理"></el-option>
              <el-option label="普通用户" value="普通用户"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="(dialogFormVisible = false,submit())">确 定</el-button>
        </div>
      </el-dialog>

<!-- 修改用户弹出层 -->
      <div class="tablehome">
        <el-dialog title="修改用户信息" :visible.sync="dialogFormVisible2">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="用户名称">
              <el-input v-model="form.username" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="用户密码">
              <el-input v-model="form.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="用户权限">
              <el-select v-model="form.id" placeholder="请选择用户权限" class="select">
                <el-option label="管理员" value="管理员"></el-option>
                <el-option label="售票员" value="售票员"></el-option>
                <el-option label="剧院经理" value="剧院经理"></el-option>
                <el-option label="普通用户" value="普通用户"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible2 = false">取 消</el-button>
            <el-button type="primary" @click="(dialogFormVisible2 = false,change())">修 改</el-button>
          </div>
        </el-dialog>
        <div class="top1">
          管理系统用户
          <div class="return">
            <el-button size="mini" type="success" @click="dialogFormVisible = true">添加用户</el-button>
          </div>
        </div>
        <div class="bottom">
          <el-table
            :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
            stripe
            style="width: 100%"
          >
            <el-table-column prop="username" label="用户名称" width="180"></el-table-column>
            <el-table-column prop="password" label="用户密码" width="180"></el-table-column>
            <el-table-column prop="identify" label="用户权限"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="(handleClick(scope.row),dialogFormVisible2 = true)"
                >编辑</el-button>
                <el-button size="mini" type="danger" @click="handleClick2(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <template>
            <div class="page">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[2,3 , 4, 5]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableData.length"
              ></el-pagination>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//请求数据
import axios from "axios";
import { userManager } from "../../network/userManager";
export default {
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogTableVisible2: false,
      dialogFormVisible2: false,
      _id: "",
      form: {
        username: "",
        password: "",
        id: "",

        delivery: false
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    //增加用户
    submit() {
      // console.log(this.form)
      axios({
        url: "http://localhost:2020/addUser",
        method: "post",
        data: {
          username: this.form.username,
          password: this.form.password,
          id: this.form.id
        }
      }).then(res => {
        console.log(res);
        alert(res.data);
        location.reload();
      });
    },

    //删除用户
    handleClick2(e) {
      // console.log(e._id)
      axios({
        url: "http://localhost:2020/deleteUser",
        method: "post",
        data: {
          id: e._id
        }
      }).then(res => {
        console.log(res);
        alert(res.data);
        location.reload();
      });
    },

    // 修改用户
    handleClick(e) {
      console.log(e._id);
      this._id = e._id;
      this.form.username = e.username;
      this.form.password = e.password;
      this.form.id = e.identify;
    },
    change() {
      axios({
        url: "http://localhost:2020/updateUser",
        method: "post",
        data: {
          id: this._id,
          username: this.form.username,
          password: this.form.password,
          id2: this.form.id
        }
      }).then(res => {
        console.log(res);
        alert(res.data);
        location.reload();
      });
    }
  },
  created() {
    //请求用户数据
    userManager().then(res => {
      console.log(res);
      this.tableData = res;
    });
  }
};
</script>

<style>
.home {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
  position: relative;
}

.top {
  width: 100%;
  height: 70px;
  line-height: 70px;
  /* border: 2px solid red; */
  color: #409eff;
  font-size: 26px;
  font-family: "华文新魏";
}

.bottom {
  width: 100%;
  height: calc(100% - 70px);
  /* border: 2px solid green; */
}

.el-table .cell {
  text-align: center !important;
}

.select {
  width: 380px !important;
}
</style>