<template>
  <div>
    <!-- 添加座位弹出层 -->
    <div class="tablehome">
      <el-dialog title="添加座位" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="座位序号" :label-width="formLabelWidth">
            <el-input v-model="form.number" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="所属影厅" :label-width="formLabelWidth">
            <el-input v-model="form.movieHall" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="座位状态" :label-width="formLabelWidth">
            <el-input v-model="form.state" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="(dialogFormVisible = false,addTable())">确 定</el-button>
        </div>
      </el-dialog>
      <div class="top">
        {{movieHallName}}
        <div class="return">
          <el-button size="mini" type="success" @click="dialogFormVisible = true">添加座位</el-button>
          <el-button size="mini" type="primary" @click="(jumpup())">返回</el-button>
          <!-- <el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button> -->
        </div>
      </div>
      <div class="bottom">
        <el-table
          :data="movieHallTableList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
          stripe
          style="width: 100%"
        >
          <el-table-column prop="tableNumber" label="影厅座位序号" width="180"></el-table-column>
          <el-table-column prop="tableHallName" label="所属影厅名称" width="180"></el-table-column>
          <el-table-column prop="tableState" label="影厅座位状态"></el-table-column>
          <el-table-column prop="address" label="操作">-----------------</el-table-column>
        </el-table>
        <template>
          <div class="page2">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[2,3 , 4, 5]"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="movieHallTableList.length"
            ></el-pagination>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import Toast from "../../components/Toast";
import axios from "axios";
export default {
  data() {
    return {
      tableData: [],
      movieHallName: "123",
      movieHallTableList: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      dialogTableVisible: false,
      dialogFormVisible: false,
      
      form: {
        number: "",
        movieHall: "",
        state: "ready",
        delivery: false
      },
      formLabelWidth: "120px"
    };
  },
  //获取后台影厅数据
  created() {
    console.log(this.$route.query.detail);
    axios({
      url: "http://localhost:2020/movieDetail",
      method: "POST",
      data: {
        movieHallName: this.$route.query.detail
      }
    }).then(res => {
      console.log(res);
      this.movieHallTableList = res.data;
    });
    this.movieHallName = this.$route.query.detail;
    this.form.movieHall = this.$route.query.detail;
  },
  destroyed () {
    // alert('实例已销毁')
  },
  methods: {
    //路由跳转（携带参数）
    jumpup() {
      this.$router.push({ path: "/admin/movies-home" });
    },

    //分页函数
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },

    //添加座位
    addTable() {
      axios({
        url: "http://localhost:2020/addTable",
        method: "post",
        data: {
          tableNumber: this.form.number,
          tableHallName: this.form.movieHall,
          tableState: this.form.state
        }
      }).then(res => {
        console.log(res);
        alert(res.data);
        location.reload();
      });
    }
  },

  //获取组件
  components: {
    toast: Toast
  }
};
</script>

<style>
.tablehome {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
  position: relative;
}

.el-dialog {
  width: 500px !important;
  border-radius: 15px !important;
}

.top {
  width: 100%;
  height: 70px;
  line-height: 70px;
  /* border: 2px solid red; */
  color: #409eff;
  font-size: 26px;
  font-family: "华文新魏";
}

.bottom {
  width: 100%;
  height: calc(100% - 70px);
  /* border: 2px solid green; */
}

.el-table .cell {
  text-align: center !important;
}

.return {
  /* position: absolute; */
  float: right;
  margin-right: 50px;
}

.page2 {
  /* border: 2px solid red; */
  margin-top: 100px;

  /* text-align: right; */
}
</style>