<template>
  <div>
    <!--  hearder 导航栏部分 -->
    <!-- 影院首页 轮播图部分 -->
    <div class="use-lbt">
      <div class="use_header">
        <div class="use_left">
          <span>
            <em>TTMS</em>
          </span> Attentively write bug theater
        </div>
        <div class="use_center"></div>
        <div class="use_right">
          <div class="item" @click="jump_01">影院首页</div>
          <div class="item" @click="jump_02">影院排片表</div>
          <div class="item" @click="jump_03">影院介绍</div>
          <div class="item active" @click="jump_04">个人中心</div>
        </div>
      </div>
      <div class="profile">
        <div class="profile-left">
          <div class="profile-top">
            <img src="../../assets/user.jpg" alt />
          </div>
          <div class="profile-text">{{id}}:{{name}}</div>
        </div>
        <div class="profile-right">
          <div class="profile-title">{{name}} 的购票信息(My ticket information)</div>
          <div class="profile-ticket">
            <el-carousel :autoplay="false" indicator-position="none">
              <el-carousel-item v-for="item in ticket" :key="item.id">
                <div class="cardWrap">
                  <div class="card cardLeft">
                    <h1>
                      Write bugs carefully
                      <span>Cinema</span>
                    </h1>
                    <div class="title">
                      <h2>What movie will you see</h2>
                      <span>movieName:{{item.ticket_movieName}}</span>
                    </div>
                    <div class="name">
                      <h2>Which movie hall ?</h2>
                      <span>{{item.ticket_movieHall}}</span>
                    </div>
                    <div class="seat">
                      <h2>{{item.ticket_table}}</h2>
                      <span>seatNumber</span>
                    </div>
                    <div class="time">
                      <h2>{{item.ticket_date}}</h2>
                      <span>Date</span>
                    </div>
                  </div>
                  <div class="card cardRight">
                    <div class="eye"></div>

                    <div class="barcode"></div>
                    <div class="number" @click="returnTicket(item)">
                      <label for>退票处理</label>
                    </div>
                  </div>
                </div>
              </el-carousel-item>
            </el-carousel>
          </div>
        </div>
      </div>
      <vue-particles
        color="#fff"
        :particleOpacity="0.7"
        :particlesNumber="60"
        shapeType="circle"
        :particleSize="4"
        linesColor="#fff"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="2"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
        class="lizi"
      ></vue-particles>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      ticket: []
    };
  },
  methods: {
    jump_01() {
      this.$router.push({ path: "/users" });
    },
    jump_02() {
      this.$router.push({ path: "/movieplan" });
    },
    jump_03() {
      this.$router.push({ path: "/introduction" });
    },
    jump_04() {
      this.$router.push({ path: "/profile" });
    },
    isLogin() {
      this.$store.commit("userStatus", sessionStorage.getItem("userInfo"));
    },
    returnTicket(e){
      console.log(e)
      //发起退票请求
      axios({
        url:'http://localhost:2020/returnTicket',
        method:"POST",
        data:{
          ticket_movieHall:e.ticket_movieHall,
          ticket_movieName:e.ticket_movieName,
          ticket_table:e.ticket_table,
          ticket_userName:e.ticket_userName,
          id:e._id
        },
      }).then(res => {
        // console.log(res)
        alert("退票成功")
        location.reload();
      })
    }
  },
  created() {
    this.isLogin();
    console.log(JSON.parse(this.$store.state.currentUser));
    this.id = JSON.parse(this.$store.state.currentUser).identify;
    this.name = JSON.parse(this.$store.state.currentUser).username;

    axios({
      url: "http://localhost:2020/userTicket",
      method: "post",
      data: {
        username: this.name
      }
    }).then(res => {
      console.log(res);
      this.ticket = res.data;
    });
  }
};
</script>

