<template>
  <div class="home1">
    <div class="top1">
      <span>{{title}}</span>
      <label for @click="tab">切换</label>
    </div>
    <div class="bottom1">
      <div class="biaoge" :style="{opacity:formShow}">
        <el-table
          :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
          stripe
          style="width: 100%"
        >
          <el-table-column prop="movieName" label="电影名称" width="180"></el-table-column>
          <el-table-column prop="movieDate" label="上映日期"></el-table-column>
          <el-table-column prop="movieClass" label="电影类型"></el-table-column>
          <el-table-column prop="movieState" label="电影状态"></el-table-column>
          <el-table-column prop="movieMoney" label="电影票房(单位：万)" width="180"></el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleClick2(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <template>
          <div class="page">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[2,3 , 4, 5]"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="tableData.length"
            ></el-pagination>
          </div>
        </template>
      </div>
      <div class="picture" :style="{opacity:pictureShow}">
        <div ref="echarts" class="simpleDemo"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { filmsList } from "../../network/films";

//引入echarts
import echarts from "echarts";

export default {
  data() {
    return {
      optionData: [],
      tableData: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      title: "影院实时票房统计（表格版）",
      formShow: 1,
      pictureShow: 0,
      form: {
        searchName: ""
      },
      //初始化echarts柱状图数据

      //初始化echarts饼状图数据
      chart: null,
      option: {
        backgroundColor: "#45515a", //背景颜色
        //标题
        title: {
          text: "上映电影在总票房中所占的比例",
          subtext: "饼图示例",
          left: "center",
          top: 20,
          textStyle: {
            color: "#ccc",
            fontStyle: "italic" //标题字体
          }
        },
        //弹窗，响应鼠标指向，显示具体细节
        tooltip: {
          trigger: "item", //以具体项目触发弹窗
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        //图例，选择要显示的项目
        legend: {
          orient: "vertical",
          left: "left",
          textStyle: {
            color: "#c8c8d0"
          },
          data: [
            "安心",
            "安盛天平",
            "中华联合保险",
            "中国太平洋保险",
            "中国人保财险",
            "平安车险"
          ] //注意要和数据的name相对应
        },
        //工具箱
        toolbox: {
          show: true, //显示工具箱
          feature: {
            //dataZoom:{show:true}, //区域缩放
            saveAsImage: { show: true } //保存图片
            //magicType:{type:['line','bar']}//动态数据切换，数据显示可以在该规定内容中切换显示方式，
          }
        },
        //数据
        series: [
          {
            name: "票房数据",
            type: "pie",
            radius: "55%",
            center: ["50%", "50%"],
            data: [],

            roseType: "radius", //角度和半径展现百分比，'area'只用半径展现
            label: {
              //饼图图形的文本标签
              normal: {
                //下同，normal指在普通情况下样式，而非高亮时样式
                textStyle: {
                  color: "rgba(255, 255, 255, 0.3)"
                }
              }
            },
            labelLine: {
              //引导线样式
              normal: {
                lineStyle: {
                  color: "rgba(255, 255, 255, 0.3)"
                },
                smooth: 0.5, //0-1，越大越平滑弯曲
                length: 10, //从块到文字的第一段长
                length2: 20 //拐弯到文字的段长
              }
            },
            width: "auto",
            height: "auto",
            itemStyle: {
              //图例样式
              normal: {
                //color: '#97413c',
                shadowBlur: 50, //阴影模糊程度
                shadowColor: "rgba(0, 0, 0, 0.5)" //阴影颜色，一般黑
              }
            },
            animationType: "scale", //初始动画效果，scale是缩放，expansion是展开
            animationEasing: "elasticOut", //初始动画缓动效果
            animationDelay: function(idx) {
              //数据更新动画时长，idx限定了每个数据块从无到有的速度
              return Math.random() * 200;
            }
          }
        ],
        color: [
          "#CCFF33",
          "#CCCCFF",
          "#CC99FF",
          "#CC9900",
          "#CC6633",
          "#33CCFF",
          "#CC3300",
          "#9933FF"
        ]
      }
    };
  },
  created() {
    filmsList().then(res => {
      console.log(res);
      var arr1 = [];
      for (let i in res) {
        if (res[i].movieState == "正在热映") {
          arr1.push(res[i]);
        }
      }
      // console.log(arr1);
      this.tableData = arr1;
      console.log(this.option.series[0].data);
      var movieNameList = [];
      // var optionData= [];
      for (let i in res) {
        if (res[i].movieState == "正在热映") {
          movieNameList.push(res[i].movieName);
          var obj = {};
          obj.name = res[i].movieName;
          obj.value = parseFloat(res[i].movieMoney);
          this.optionData.push(obj);
        }
      }
      console.log(this.optionData);
      this.option.series[0].data = this.optionData;
      this.option.legend.data = movieNameList;
      this.getPage();
    });
  },
  methods: {
    getPage() {
      this.chart = echarts.init(this.$refs.echarts);
      //  this.option.series[0].data = this.optionData;
      // 使用刚指定的配置项和数据显示图表。
      this.chart.setOption(this.option);
    },
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    tab() {
      if (this.title == "影院实时票房统计（表格版）") {
        this.title = "影院实时票房统计（饼图版）";
        this.formShow = 0;
        this.pictureShow = 1;
      } else {
        this.title = "影院实时票房统计（表格版）";
        this.formShow = 1;
        this.pictureShow = 0;
      }
    }
  }
};
</script>

<style>
.home1 {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
  position: relative;
}

.top1 {
  width: 100%;
  height: 70px;
  line-height: 70px;
  /* border: 2px solid red; */
  color: #409eff;
  font-size: 26px;
  font-family: "华文新魏";
  border-bottom: 2px solid rgb(224, 221, 221);
}

.top1 label {
  color: rgb(170, 167, 152);
  font-size: 20px;
  cursor: pointer;
  text-decoration: underline;
}

.top1 label:hover {
  color: gold;
}

.bottom1 {
  position: relative;
  /* margin-top: 30px; */
  width: 100%;
  height: calc(100% - 70px);
  /* border: 2px solid green; */
}

.page {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50px;
}

.biaoge {
  position: absolute;
  width: 100%;
  height: 100%;
}

.picture {
  position: absolute;
  width: 100%;
  height: 100%;
  /* position: relative; */
  /* border: 2px solid green; */
  box-sizing: border-box;
  display: flex;
}

.simpleDemo {
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
  box-sizing: border-box;
}
</style>