<template>
  <div>
    <!--  hearder 导航栏部分 -->
    <!-- 影院首页 轮播图部分 -->
    <div class="use-lbt">
      <div class="use_header">
        <div class="use_left">
          <span>
            <em>TTMS</em>
          </span> Attentively write bug theater
        </div>
        <div class="use_center"></div>
        <div class="use_right">
         <div class="item" @click="jump_01">影院首页</div>
          <div class="item  active" @click="jump_02">影院排片表</div>
          <div class="item" @click="jump_03">影院介绍</div>
          <div class="item" @click="jump_04">个人中心</div>
        </div>
      </div>

      <div class="table_one">
        <div class="user-left1">
        <div class="movie-top">影院排片表</div>
        <div class="movie-bottom">
          <el-table
            :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
            style="width: 100%"
          >
            <el-table-column label="剧目名称" width="200">
              <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.movieName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="存票" width="150">
              <template slot-scope="scope">
                <!-- <el-popover trigger="hover" placement="top"> -->
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.ticketList }}</el-tag>
                </div>
                <!-- </el-popover> -->
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" @click="(handleClick(scope.row))">抢票</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      </div>

      <vue-particles
        color="#fff"
        :particleOpacity="0.7"
        :particlesNumber="60"
        shapeType="circle"
        :particleSize="4"
        linesColor="#fff"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="2"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
        class="lizi"
      ></vue-particles>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { filmsList,duceList } from "../../network/films";
export default {
  data() {
    return {
      tableData: [],
      currentPage: 1,
      pagesize: 5,
      currpage: 1,
      lbtarr:[]
    };
  },
  methods: {
     jump_01(){
      this.$router.push({'path': '/users'})
    },
    jump_02(){
      this.$router.push({'path': '/movieplan'})
    },
    jump_03(){
      this.$router.push({'path': '/introduction'})
    },
    jump_04(){
      this.$router.push({'path': '/profile'})
    },
    handleClick(e){
      // console.log(e)
      this.$router.push({name: 'Detail',query:e})
    }
  },
  created() {
    // alert(JSON.parse(this.$store.state.currentUser).username)


    filmsList().then(res => {
      
      var arr_one = res;
      duceList().then(res => {
      var newArray = [];
      for(let i = 0; i < res.length; i ++){
          for(let j = 0; j < arr_one.length; j++){
            if(res[i].movieName == arr_one[j].movieName){
              newArray.push(Object.assign({},res[i],arr_one[j]))
            }
          }
      }

     var arr = []; 
      for (let i in newArray) {
        if (newArray[i].movieState == "正在热映") {
          arr.push(newArray[i]);
        }
      }
      this.arr1 = arr;
      this.tableData = this.arr1;
    })
      
    });

  
  }
};
</script>

